import { useState, useEffect, useContext, useCallback } from "react";
import { getSleepDurationStatistics } from "@/api/deviceReports";
import ls from "store2";
import { durationData } from "./mockData";
import BarChart from "@/Components/GraphAndChart/barChart";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import dayjs from "dayjs";
import { use } from "echarts/lib/extension";
import { CustomContext } from "@/Context/UseCustomContext";
export default function DurationStatistic({ isSummaryBtn = true }) {
  const { title, color, icon, dataAnalysis, summaryProps } =
    getTemplateData("Duration");
  const context = useContext(CustomContext);
  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails = {},
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(31, "day").format("YYYY-MM-DD")
  );
  const [sleepDurationData, setSleepDurationData] = useState([]);
  const getSleepDurationData = useCallback(() => {
    getSleepDurationStatistics({
      uids: elderlyDetails?.bedRoomIds,
      elderly_id: elderlyDetails._id,
      to_date: fromDate,
      from_date: toDate,
    })
      .then((res) => {
        setSleepDurationData(res.data);
        console.log("sleep duration data ===============>", res);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [toDate, chartToDate, elderlyDetails]);

  useEffect(() => {
    getSleepDurationData();
  }, [getSleepDurationData]);
  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate]);
  return (
    <Template
      title={title}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      isSummaryBtn={isSummaryBtn}
      summaryProps={summaryProps}
    >
      <BarChart
        data={sleepDurationData}
        dataType="duration"
        color={color}
        toDate={toDate}
        fromDate={fromDate}
        chartFor={summaryProps.chartFor}
      />
    </Template>
  );
}
